.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-content {
    display: flex;

    .sort-animation-container {
        margin-top: 20px;

        display: flex;
        flex-direction: column;
        .sort-animation {
            display: flex;
            flex-direction: column;

            .header {

            }
            .visualization {

            }

            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }

        margin-bottom: 20px;
    }

    .action-container {
        .action-button  {
            border-radius: 20px;
        }
    }
}


